/* Base */
body {
    line-height: 1.7;
    color: gray;
    font-weight: 300;
    font-size: 1rem;
    background: #fff;
    font-family: "Muli", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"; }
  
  ::-moz-selection {
    background: #000;
    color: #fff; }
  
  ::selection {
    background: #000;
    color: #fff; }
  
  a {
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    color: #2f89fc; }
    a:hover {
      color: #046df4;
      text-decoration: none; }
  
  h1, h2, h3, h4, h5,
  .h1, .h2, .h3, .h4, .h5 {
    font-family: "Muli", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    line-height: 1.5;
    color: #000; }
  
  .border-2 {
    border-width: 2px; }
  
  .text-black {
    color: #000 !important; }
  
  .bg-black {
    background: #000 !important; }
  
  .color-black-opacity-5 {
    color: rgba(0, 0, 0, 0.5); }
  
  .color-white-opacity-5 {
    color: rgba(255, 255, 255, 0.5); }
  
  .site-wrap:before {
    -webkit-transition: .3s all ease-in-out;
    -o-transition: .3s all ease-in-out;
    transition: .3s all ease-in-out;
    background: rgba(0, 0, 0, 0.6);
    content: "";
    position: absolute;
    z-index: 2000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    visibility: hidden; }
  
  .offcanvas-menu .site-wrap {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 2;
    overflow: hidden; }
    .offcanvas-menu .site-wrap:before {
      opacity: 1;
      visibility: visible; }
  
  .btn {
    border-radius: 0; }
    .btn:hover, .btn:active, .btn:focus {
      outline: none;
      -webkit-box-shadow: none !important;
      box-shadow: none !important; }
  
  .line-height-1 {
    line-height: 1 !important; }
  
  .bg-black {
    background: #000; }
  
  .site-section {
    padding: 2.5em 0; }
    @media (min-width: 768px) {
      .site-section {
        padding: 5em 0; } }
    .site-section.site-section-sm {
      padding: 4em 0; }
  
  .section-heading h2 {
    font-weight: 700;
    color: #000; }
  
  .border-top {
    border-top: 1px solid #edf0f5 !important; }
  
  .site-footer {
    padding: 4em 0;
    background: #333333; }
    @media (min-width: 768px) {
      .site-footer {
        padding: 5em 0; } }
    .site-footer .border-top {
      border-top: 1px solid rgba(255, 255, 255, 0.1) !important; }
    .site-footer p {
      color: #737373; }
    .site-footer h2, .site-footer h3, .site-footer h4, .site-footer h5 {
      color: #fff; }
    .site-footer a {
      color: #999999; }
      .site-footer a:hover {
        color: white; }
    .site-footer ul li {
      margin-bottom: 10px; }
    .site-footer .footer-heading {
      font-size: 16px;
      color: #fff; }
  
  .bg-text-line {
    display: inline;
    background: #000;
    -webkit-box-shadow: 20px 0 0 #000, -20px 0 0 #000;
    box-shadow: 20px 0 0 #000, -20px 0 0 #000; }
  
  .text-white-opacity-05 {
    color: rgba(255, 255, 255, 0.5); }
  
  .text-black-opacity-05 {
    color: rgba(0, 0, 0, 0.5); }
  
  .hover-bg-enlarge {
    overflow: hidden;
    position: relative; }
    @media (max-width: 991.98px) {
      .hover-bg-enlarge {
        height: auto !important; } }
    .hover-bg-enlarge > div {
      -webkit-transform: scale(1);
      -ms-transform: scale(1);
      transform: scale(1);
      -webkit-transition: .8s all ease-in-out;
      -o-transition: .8s all ease-in-out;
      transition: .8s all ease-in-out; }
    .hover-bg-enlarge:hover > div, .hover-bg-enlarge:focus > div, .hover-bg-enlarge:active > div {
      -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
      transform: scale(1.2); }
    @media (max-width: 991.98px) {
      .hover-bg-enlarge .bg-image-md-height {
        height: 300px !important; } }
  
  .bg-image {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed; }
    .bg-image.overlay {
      position: relative; }
      .bg-image.overlay:after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        background: rgba(0, 0, 0, 0.7); }
    .bg-image > .container {
      position: relative;
      z-index: 1; }
  
  @media (max-width: 991.98px) {
    .img-md-fluid {
      max-width: 100%; } }
  
  @media (max-width: 991.98px) {
    .display-1, .display-3 {
      font-size: 3rem; } }
  
  .overlap-to-top {
    margin-top: -150px; }
  
  .ul-check {
    margin-bottom: 50px; }
    .ul-check li {
      position: relative;
      padding-left: 35px;
      margin-bottom: 15px;
      line-height: 1.5; }
      .ul-check li:before {
        left: 0;
        font-size: 20px;
        top: -.3rem;
        font-family: "icomoon";
        content: "\e5ca";
        position: absolute; }
    .ul-check.white li:before {
      color: #fff; }
    .ul-check.success li:before {
      color: #8bc34a; }
    .ul-check.primary li:before {
      color: #2f89fc; }
  
  /* Blocks */
  .container {
    max-width: 1200px; }
  
  .site-cover {
    background-size: cover;
    background-position: top center;
    position: relative; }
    .site-cover.overlay:before {
      background: rgba(0, 0, 0, 0.6);
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      right: 0;
      bottom: 0; }
    .site-cover, .site-cover .same-height {
      padding: 3em 0; }
    .site-cover .post-entry .post-meta {
      color: #ffffff;
      font-size: 13px; }
      .site-cover .post-entry .post-meta .author-figure img {
        width: 30px;
        border-radius: 50%; }
    .site-cover .post-entry h2 {
      font-size: 2rem;
      line-height: 1.2;
      color: #ffffff; }
      @media (min-width: 992px) {
        .site-cover .post-entry h2 {
          font-size: 3rem; } }
      .site-cover .post-entry h2 a {
        color: #ffffff; }
    .site-cover.single-page h1 {
      font-size: 2rem;
      color: #ffffff; }
      @media (min-width: 992px) {
        .site-cover.single-page h1 {
          font-size: 5rem; } }
      .site-cover.single-page h1 a {
        color: #ffffff; }
  
  .post-meta {
    font-size: 13px;
    color: #b3b3b3; }
  
  
  
  .post-category {
    display: inline-block;
    padding: 2px 10px;
    border-radius: 4px;
    letter-spacing: .2em;
    font-weight: 700;
    font-size: .6rem;
    text-transform: uppercase; }
  
  .retro-layout .gradient {
    position: relative; }
    
  .entry2 img {
    margin-bottom: 30px; }
  
  .entry2 h2 {
    font-size: 24px;
    margin-bottom: 30px;
    font-weight: 700; }
    .entry2 h2 a {
      color: #000; }
  
  .entry2 .post-meta {
    color: #b3b3b3;
    font-size: 13px;
    width: 100%;
    display: block; }
    .entry2 .post-meta a {
      color: #000; }
    .entry2 .post-meta .author-figure img {
      width: 30px;
      border-radius: 50%; }
  
  .entry3, .entry4 {
    margin-bottom: 30px; }
    .entry3 .text h2, .entry4 .text h2 {
      font-size: 18px;
      line-height: 1.5;
      font-weight: 700; }
      .entry3 .text h2 a, .entry4 .text h2 a {
        color: #000; }
    .entry3 .figure, .entry4 .figure {
      -webkit-box-flex: 0;
      -ms-flex: 0 0 150px;
      flex: 0 0 150px;
      -webkit-transition: .3s all ease;
      -o-transition: .3s all ease;
      transition: .3s all ease;
      opacity: 1; }
    .entry3:hover figure, .entry3:focus figure, .entry4:hover figure, .entry4:focus figure {
      opacity: .5; }
  
  .entry4 {
    margin-bottom: 30px; }
    .entry4 .text h2 {
      font-size: 16px;
      line-height: 1.5;
      font-weight: 700; }
      .entry4 .text h2 a {
        color: #000; }
    .entry4 .figure {
      -webkit-box-flex: 0;
      -ms-flex: 0 0 100px;
      flex: 0 0 100px; }
  
  